


























import Vue from 'vue'
export default Vue.extend({
  props: {
    disabled: {
      type: Boolean,
      default: false,
    }
  },
  data () {
    return {
      isEditing: false,
    }
  },
  watch: {
    isEditing (v) {
      this.$nextTick(() => {
        const el = (this.$refs.editorDiv as HTMLElement)
        const focusable = el && el.querySelector('input, a, select')

        if (!focusable) return

        if (v) {
          setTimeout(() => (focusable as HTMLInputElement).select(), 1)
        }
      })
    }
  },
  methods: {
    blur () {
      this.$nextTick(() => {
        this.isEditing = false;
      })
    },
    handleFocus () {
      if (!this.disabled) {
        this.isEditing = true
      }
    }
  }
})
