













import Vue from 'vue'
import _ from 'lodash'

export default Vue.extend({
  props: {
    columnWidth: {
      type: Number,
      required: true,
    },
    n: {
      type: Number,
      required: true,
    },
    height: {
      type: Number,
      required: true,
    }
  },
  computed: {
    range (): Array<Number> {
      return _.range(0, this.n)
    }
  }
})
