










import Vue from 'vue'
import store from '@/store'
import _ from 'lodash'

export default Vue.extend({
  computed: {
    time(): number {
      const d = new Date((store.state as any).time.time)
      const e = new Date((store.state as any).time.time)
      d.setHours(0, 0, 0)
      return e.getTime() - d.getTime()
    }
  },
  props: {
    columnWidth: {
      type: Number,
      required: true,
    },
    height: {
      type: Number,
      required: true,
    }
  },
})
